module.exports = "// Given a string, find the length of the longest substring without repeating characters.\n\n// Example 1:\n\n// Input: \"abcabcbb\"\n// Output: 3 \n// Explanation: The answer is \"abc\", with the length of 3. \n// Example 2:\n\n// Input: \"bbbbb\"\n// Output: 1\n// Explanation: The answer is \"b\", with the length of 1.\n// Example 3:\n\n// Input: \"pwwkew\"\n// Output: 3\n// Explanation: The answer is \"wke\", with the length of 3. \n//              Note that the answer must be a substring, \"pwke\" is a subsequence and not a substring.\n\n/**\n * @param {string} s\n * @return {number}\n */\n\n// time complexity O(n)\nvar lengthOfLongestSubstring = function(s) {\n   let lookup = {};\n   let start = 0;\n   let max = 0;\n   for (let i = 0 ; i < s.length; i++) {\n       if (lookup[s[i]] !== undefined && lookup[s[i]] >= start) {\n           start = lookup[s[i]] + 1;\n       }\n\n       lookup[s[i]] = i;\n       max = Math.max(max, i - start + 1);\n   }\n   return max;\n};\n\n\nconsole.log(lengthOfLongestSubstring('pwwkew'));\nconsole.log(lengthOfLongestSubstring('abba'));\nconsole.log(lengthOfLongestSubstring('raceracecar'));\nconsole.log(lengthOfLongestSubstring('obamacare'));"