module.exports = "// Given a binary tree, determine if it is a valid binary search tree (BST).\n\n// Assume a BST is defined as follows:\n\n// The left subtree of a node contains only nodes with keys less than the node's key.\n// The right subtree of a node contains only nodes with keys greater than the node's key.\n// Both the left and right subtrees must also be binary search trees.\n// Example 1:\n\n// Input:\n//     2\n//    / \\\n//   1   3\n// Output: true\n// Example 2:\n\n//     5\n//    / \\\n//   1   4\n//      / \\\n//     3   6\n// Output: false\n// Explanation: The input is: [5,1,4,null,null,3,6]. The root node's value\n//              is 5 but its right child's value is 4.\n\n// inorder traversal\nconst isValidBST = root => {\n    let result = [];\n    const traversal = head => {\n        if (head === null) return head;\n        traversal(head.left);\n        result.push(head.val);\n        traversal(head.right);\n    }\n    traversal(root);\n    \n    let slow = 0;\n    let fast = 1;\n    while (fast <= result.length - 1) {\n        if (result[slow] >= result[fast]) return false;\n        slow++;\n        fast++;\n    }\n    return true;\n}\n// ----------------------------------------------------------\nvar isValidBST2 = root => helper(root, null, null)\n\nvar helper = (root, min, max) => {\n  if (root === null) return true\n  if (min !== null && root.val <= min) return false\n  if (max !== null && root.val >= max) return false\n  return helper(root.left, min, root.val) && helper(root.right, root.val, max)\n}\n\nconsole.log(isValidBST(null));\nconsole.log(isValidBST2(null));"