module.exports = "// Given a binary search tree, write a function kthSmallest to find the kth smallest element in it.\n\n// Note: \n// You may assume k is always valid, 1 ≤ k ≤ BST's total elements.\n\n// Example 1:\n\n// Input: root = [3,1,4,null,2], k = 1\n//    3\n//   / \\\n//  1   4\n//   \\\n//    2\n// Output: 1\n// Example 2:\n\n// Input: root = [5,3,6,2,4,null,null,1], k = 3\n//        5\n//       / \\\n//      3   6\n//     / \\\n//    2   4\n//   /\n//  1\n// Output: 3\n\nconst kthSmallest = (root, k) => {\n    let counter = 0;\n    let kth = null;\n    let inorderTraversal = node => {\n        if (node.left !== null) { inorderTraversal(node.left); }\n        counter++;\n        if (counter === k) { kth = node; }\n        if (node.right !== null) { inorderTraversal(node.right); }\n    }\n    inorderTraversal(root);\n    return kth.val;\n}\n\nconsole.log(kthSmallest(null,0));"