module.exports = "// Given an array nums, write a function to move all 0's to the end of it while maintaining the relative order of the non-zero elements.\n\n// Example:\n\n// Input: [0,1,0,3,12]\n// Output: [1,3,12,0,0]\n// Note:\n\n// You must do this in-place without making a copy of the array.\n// Minimize the total number of operations.\n\nvar moveZeroes = function(nums) {  \n    for (let i = nums.length; i > 0; i--) {\n        if(nums[i] === 0) {\n            let temp = nums.splice(i, 1);\n            nums.push(temp);\n        }\n    }\n}\nconsole.log(moveZeroes([0]));"