module.exports = "// You need to find the largest value in each row of a binary tree.\n\n// Example:\n// Input: \n//           1\n//          / \\\n//         3   2\n//        / \\   \\  \n//       5   3   9 \n\n// Output: [1, 3, 9]\n\n// level order traversal - BFS\n\nvar largestValues = function(root) {\n    // sanity check\n    if (root === null) return [];\n\n    let result = [];\n    let queue = [root];\n    while (queue.length > 0){\n        let size = queue.length;\n        let current = [];\n        for (let i = 0; i < size; i++) {\n            let head = queue.shift();\n            current.push(head.val);\n            if (head.left !== null) { queue.push(head.left); }\n            if (head.right !== null) { queue.push(head.right); }\n        }\n        result.push(Math.max(...current));\n    }\n    return result;\n}\nconsole.log(largestValues(null));"