module.exports = "// Given an array of meeting time intervals consisting of start and end times [[s1,e1],[s2,e2],...] (si < ei), \n// determine if a person could attend \n\n// For example,\n// Given [[0, 30],[5, 10],[15, 20]],\n// return false.\n// --------------------- \n// 作者：fantasiasango \n// 来源：CSDN \n// 原文：https://blog.csdn.net/fantasiasango/article/details/52698405 \n// 版权声明：本文为博主原创文章，转载请附上博文链接！\n\n// sweepline\n// brute force\n\n// interval = (start, end)\n//          interva1  inverval2  interval3\n// input2 : [0, 10],  [15, 20],  [0, 30], [5, 8]\n\n\n// [0..........10]\n//                     [15.....20]                    \n// [0.................................30]\n//        [5..8]\n// ---------------------------------------------       \n// [0..........10]\n//                  [11]..................30]\n//                   ^   \n//                    [15.....20]                    \n\n\n// public boolean canAttendMeetings(Interval[] intervals) {\n//     o(log n)  > o(n)\n//     Arrays.sort(intervals, new Comparator<Interval>() {\n//         public int compare(Interval a, Interval b) {\n//             return a.start - b.start;\n//         };\n//     });\n//     // sorted interal array \n//     // sweepline\n//     for (int i = 1; i < intervals.length; i++) {\n//         if (intervals[i - 1].end > intervals[i].start) {\n//             return false;\n//         }\n//     }\n// }\n\nfunction Interval(start, end) {\n     this.start = start;\n     this.end = end;\n}\n\nvar canAttendMeetings = function(intervals) {\n    let data = [];\n    for (let i = 0; i < intervals.length; i++) {\n        data.push(new Interval(intervals[i][0],intervals[i][1]));\n    }\n    data.sort((a,b) => {\n        return a.start > b.start ? 1 : -1;\n    });\n    for(var i = 1; i < data.length; i++) {\n        if(data[i - 1].end > data[i].start) {\n            return false;\n        }\n    }\n    return true;\n};\n\nconsole.log(canAttendMeetings([[5, 10],[0, 30],[15, 20]]));"