module.exports = "// Given a sorted array nums, remove the duplicates in-place such that each element appear only once and return the new length.\n\n// Do not allocate extra space for another array, you must do this by modifying the input array in-place with O(1) extra memory.\n\n// Example 1:\n\n// Given nums = [1,1,2],\n\n// Your function should return length = 2, with the first two elements of nums being 1 and 2 respectively.\n\n// It doesn't matter what you leave beyond the returned length.\n// Example 2:\n\n// Given nums = [0,0,1,1,1,2,2,3,3,4],\n\n// Your function should return length = 5, with the first five elements of nums being modified to 0, 1, 2, 3, and 4 respectively.\n\n// It doesn't matter what values are set beyond the returned length.\n\n/**\n * @param {number[]} nums\n * @return {number} the length or nums;\n */\n\nvar removeDuplicates = function(nums) {\n    if (!nums.length) return 0;\n    let count = 1;\n    for (let i=1; i<nums.length; i++) {\n        if (nums[i] !== nums[i-1]) {\n            nums[count] = nums[i];\n            count++;\n        }\n    }\n    return count;\n};\n\nconsole.log(removeDuplicates(null));"