module.exports = "// Given a sorted linked list, delete all duplicates such that each element appear only once.\n\n// Example 1:\n\n// Input: 1->1->2\n// Output: 1->2\n// Example 2:\n\n// Input: 1->1->2->3->3\n// Output: 1->2->3\n\nconst deleteDuplicates = head => { \n    if (head === null) { return head; }\n    let current = head;\n    while (current) {\n        if(current.next && current.next.val === current.val) {\n            current.next = current.next.next;\n        } else {\n            current = current.next;\n        }\n    }\n    return head;\n}\nconsole.log(deleteDuplicates(null));"