module.exports = "// Given a sorted (in ascending order) integer array nums of n elements and a target value, write a function to search target in nums. If target exists, then return its index, otherwise return -1.\n\n// Example 1:\n\n// Input: nums = [-1,0,3,5,9,12], target = 9\n// Output: 4\n// Explanation: 9 exists in nums and its index is 4\n\nconst search = (nums, target) => {\n    let start = 0, end = nums.length - 1, middle;\n    while (start < end) {\n        middle = Math.floor((start+end) / 2);\n        if (nums[middle] === target) { return middle; }\n        if (nums[middle] < target) {\n            start = middle + 1;\n        } else { \n            end = middle - 1;\n        }\n    }\n    return -1;\n}\nconsole.log(search([1,2,3,4,5,6], 9));"