module.exports = "const data = [  \n    {  \n       \"date\":\"2018-01-29\",\n       \"category\":\"apple\",\n       \"count\":\"1\"\n    },\n    {  \n       \"date\":\"2018-01-29\",\n       \"category\":\"apple\",\n       \"count\":\"1\"\n    },\n    {  \n       \"date\":\"2018-01-28\",\n       \"category\":\"apple\",\n       \"count\":\"2\"\n    },\n    {  \n       \"date\":\"2018-01-27\",\n       \"category\":\"apple\",\n       \"count\":\"1\"\n    },\n    {  \n       \"date\":\"2018-01-29\",\n       \"category\":\"banana\",\n       \"count\":\"2\"\n    },\n    {  \n       \"date\":\"2018-01-27\",\n       \"category\":\"banana\",\n       \"count\":\"3\"\n    },\n    {  \n       \"date\":\"2018-01-27\",\n       \"category\":\"banana\",\n       \"count\":\"2\"\n    },\n    {  \n       \"date\":\"2018-01-28\",\n       \"category\":\"orange\",\n       \"count\":\"1\"\n    },\n    {  \n       \"date\":\"2018-01-29\",\n       \"category\":\"orange\",\n       \"count\":\"4\"\n    },\n    {  \n       \"date\":\"2018-01-24\",\n       \"category\":\"orange\",\n       \"count\":\"5\"\n    }\n ];\n\n\n const groupCategory = () => {\n  let result = {};\n  for (let i = 0; i < data.length; i++) {\n    if (result[data[i].category] === undefined) {\n      result[data[i].category] = [ {date: data[i].date, count: data[i].count} ]\n    } else {\n      let temp = result[data[i].category];\n      let match = false;\n      for (let j = 0; j < temp.length; j++) {\n          if (temp[j].date === data[i].date) {\n              temp[j].count = (parseInt(temp[j].count) + parseInt(data[i].count)).toString();\n              match = true;\n          }\n      }\n      if (!match) {\n          temp.push({date: data[i].date, count: data[i].count});\n      }\n    }\n  }\n  return result;\n}\n console.log(groupCategory());\n   "