module.exports = "// Given a tree, rearrange the tree in in-order so that the leftmost node in the tree is now the root of the tree, and every node has no left child and only 1 right child.\n\n// Example 1:\n// Input: [5,3,6,2,4,null,8,1,null,null,null,7,9]\n\n//        5\n//       / \\\n//     3    6\n//    / \\    \\\n//   2   4    8\n//  /        / \\ \n// 1        7   9\n\n// Output: [1,null,2,null,3,null,4,null,5,null,6,null,7,null,8,null,9]\n\nfunction TreeNode(val) {\n    this.val = val;\n    this.left = this.right = null;\n}\nvar increasingBST = function(root) {\n    let result = [];\n    const traversal = head => {\n        if (head === null) return head;\n        traversal(head.left);\n        result.push(head.val);\n        traversal(head.right);\n    }\n    traversal(root);\n    \n    let dummyTree = new TreeNode(0);\n    let current = dummyTree;\n    for (let i = 0; i < result.length; i++) {\n        current.right = new TreeNode(result[i]);\n        current = current.right;\n    }\n    return dummyTree.right;\n}\n\nconst n1 = new TreeNode(3);\nconst n2 = new TreeNode(9);\nconst n3 = new TreeNode(20);\nconst n4 = new TreeNode(15);\nconst n5 = new TreeNode(7);\n\nn1.left = n2;\nn1.right = n3;\nn3.left = n4;\nn3.right = n5;\n\nconsole.log(increasingBST(n1));"