module.exports = "// Given a 32-bit signed integer, reverse digits of an integer.\n\n// Example 1:\n// Input: 123\n// Output: 321\n\n// Example 2:\n// Input: -123\n// Output: -321\n\n// Example 3:\n// Input: 120\n// Output: 21\n\nconst reverse = number => {\n    let y = Math.abs(number);\n    if(y.toString(2).length > 31) return 0;\n    let arr = y.toString().split(\"\").reverse();\n    if(Number(arr.join(\"\")).toString(2).length > 31) return 0;\n    let z = Number(arr.join(\"\"));\n    return number >= 0 ? z : -z;\n}\n\nconsole.log(reverse(123));"