module.exports = "// Given an array nums of n integers, are there elements a, b, c in nums such that a + b + c = 0? Find all unique triplets in the array which gives the sum of zero.\n\n// Note:\n\n// The solution set must not contain duplicate triplets.\n\n// Example:\n\n// Given array nums = [-1, 0, 1, 2, -1, -4],\n\n// A solution set is:\n// [\n//   [-1, 0, 1],\n//   [-1, -1, 2]\n// ]\n\n// const twoSum = (nums, leftpointer, rightpointer, target, reuslt) => {\n//   while(leftpointer < rightpointer) {\n//     if (nums[leftpointer] + nums[rightpointer] === target) {\n//       reuslt.push([nums[leftpointer], nums[rightpointer], -target]);\n//       console.log(nums, leftpointer, rightpointer, target, reuslt);\n//       leftpointer++;\n//       rightpointer--;\n//     } else if (nums[leftpointer] + nums[rightpointer] < target) {\n//       leftpointer++;\n//     } else {\n//       rightpointer--;\n//     }\n//   }\n\n// }\n\n\n\nconst twoSum = (nums, target, result) => {\n  let start = 0;\n  let end = nums.length - 1;\n  while (start < end) {\n      let sum = nums[start] + nums[end];\n      if(sum === target) { result.push([nums[start], nums[end] , -target]); return [start + 1,end + 1] }\n      else if (sum < target) {\n          start++;\n      } else {\n          end--;\n      }\n  }\n  return null;\n}\nvar threeSum = function(nums) {\n    let result = [];\n    nums.sort((a, b) =>  a - b );\n    for (let i = 0; i < nums.length - 1; i++) {\n      if (i > 0 && nums[i] === nums[i - 1]) {\n          continue;\n      }\n        twoSum(nums, -nums[i], result)\n    }\n    return result;\n};\n\nconsole.log(threeSum([-1, 0, 1, 2, -1, -4]));"