module.exports = "// You are given two non-empty linked lists representing two non-negative integers. The digits are stored in reverse order and each of their nodes contain a single digit. Add the two numbers and return it as a linked list.\n\n// You may assume the two numbers do not contain any leading zero, except the number 0 itself.\n\n// Example:\n\n// Input: (2 -> 4 -> 3) + (5 -> 6 -> 4)\n// Output: 7 -> 0 -> 8\n// Explanation: 342 + 465 = 807.\n\n/**\n * @param {ListNode} l1\n * @param {ListNode} l2\n * @return {ListNode}\n */\nconst ListNode = val => {\n    this.val = val;\n    this.next = null;\n};\nvar addTwoNumbers = function(l1, l2) {\n    let twoSum = new ListNode(0);\n    let cur = twoSum;\n    let carryOver = 0;\n    while(l1 || l2) {\n       let val1 = l1 && l1.val !== null ? l1.val : 0;\n       let val2 = l2 && l2.val !== null ? l2.val : 0;\n       let sum = val1 + val2 + carryOver;\n       if (sum >= 10) {\n          cur.next = new ListNode(sum % 10);\n          carryOver = Math.floor(sum / 10);\n       } else {\n          cur.next = new ListNode(sum);\n          carryOver = 0;\n       }\n       cur = cur.next;\n       l1 = l1 && l1.next;\n       l2 = l2 && l2.next;\n    }\n    if (carryOver > 0) {\n        cur.next = new ListNode(carryOver);\n    }\n    return twoSum.next;\n  };\n\n  console.log(addTwoNumbers(null, null));"