module.exports = "\n// Given a collection of integers that might contain duplicates, nums, return all possible subsets (the power set).\n\n// Note: The solution set must not contain duplicate subsets.\n\n// Example:\n\n// Input: [1,2,2]\n// Output:\n// [\n//   [2],\n//   [1],\n//   [1,2,2],\n//   [2,2],\n//   [1,2],\n//   []\n// ]\n\nvar dfs = function(nums,index, temp, res){\n    let lastpopped\n    res.push(temp.slice());\n    for (let i=index;i<nums.length;i++){        \n        if(nums[i] === lastpopped) { continue; }\n        temp.push(nums[i]);\n        dfs(nums, i + 1, temp, res);\n        lastpopped = temp.pop();\n    }\n}\n\nvar subsetsWithDup = function(nums) {\n    let result = [];\n    if (nums.length === 0 && nums === null) return result;\n    nums.sort((a,b) => a - b);\n    dfs(nums, 0, [], result);\n    return result;\n};\n\nconsole.log(subsetsWithDup([1,2,2]));\n\n// --------------------------------------------\n\nconst dfs2 = (nums, index, subset, result) =>{\n    result.push(subset.slice());\n    for (let i = index; i < nums.length; i++){\n        if (i !== index && nums[i] === nums[i-1]) continue;\n        subset.push(nums[i]);\n        dfs2(nums, i + 1, subset, result);\n        subset.pop();\n    }\n}\nconst subsetsWithDup2 = nums => {\n    if (nums === null || nums.length === 0) { return []; }\n    nums.sort((a, b) =>  a - b);\n    let result = [];\n    dfs2(nums, 0, [], result);\n    return result;\n}\nconsole.log(subsetsWithDup2([1,2,2]));"