module.exports = "// Given an 2D board, count how many battleships are in it. The battleships are represented with 'X's, empty slots are represented with '.'s. You may assume the following rules:\n// You receive a valid board, made of only battleships or empty slots.\n// Battleships can only be placed horizontally or vertically. In other words, they can only be made of the shape 1xN (1 row, N columns) or Nx1 (N rows, 1 column), where N can be of any size.\n// At least one horizontal or vertical cell separates between two battleships - there are no adjacent battleships.\n// Example:\n// X..X\n// ...X\n// ...X\n// In the above board there are 2 battleships.\n// Invalid Example:\n// ...X\n// XXXX\n// ...X\n// This is an invalid board that you will not receive - as battleships will always have a cell separating between them.\n\n/**\n * @param {character[][]} board\n * @return {number}\n */\n\nconst updateBoard = (board, x, y) => {\n    if (x < 0 || x >= board.length || y < 0 || y >= board[0].length || board[x][y] !== 'X') { return; }\n    board[x][y] = '.';\n    updateBoard(board, x+1, y);\n    updateBoard(board, x-1, y);\n    updateBoard(board, x, y+1);\n    updateBoard(board, x, y-1);\n}\nvar countBattleships = function(board) {\n    let count = 0;\n    for (let i = 0; i < board.length; i++){\n        for (let j = 0; j < board[0].length; j++) {\n            if (board[i][j] === 'X') {\n                count++;\n                updateBoard(board, i, j);\n            }\n        }\n    }\n    return count;\n};\nconst board = [[\"X\",\".\",\".\",\"X\"],[\".\",\".\",\".\",\"X\"],[\".\",\".\",\".\",\"X\"]];\nconsole.log(countBattleships(board));\n\nconst countBattleships2 = board => {\n    let count = 0;\n    for (let i = 0; i < board.length; i++) {\n        for (let j = 0; j < board[0].length; j++ ) {\n            if (board[i][j] === '.') { continue; }\n            if (i > 0 && board[i - 1][j] === 'X') { continue; }\n            if (j > 0 && board[i][j - 1] === 'X') { continue; }\n            count++;\n        }\n    }\n    return count;\n}\nconst board2 = [[\"X\",\".\",\".\",\"X\"],[\".\",\".\",\"X\",\"X\",\"X\"],[\".\",\".\",\".\",\"X\"]];\nconsole.log(countBattleships2(board2));\n"