module.exports = "// Given a binary tree, return the zigzag level order traversal of its nodes' values. (ie, from left to right, then right to left for the next level and alternate between).\n\n// For example:\n// Given binary tree [3,9,20,null,null,15,7],\n//     3\n//    / \\\n//   9  20\n//     /  \\\n//    15   7\n// return its zigzag level order traversal as:\n// [\n//   [3],\n//   [20,9],\n//   [15,7]\n// ]\n\nvar zigzagLevelOrder = function(root) {\n    if (root === null) return [];\n    let result = [];\n    let queue = [root];\n    let normal = true;\n    while (queue.length > 0) {\n        let size = queue.length;\n        let current = [];\n        for (let i = 0; i < size; i++) {\n            let head = queue.shift();\n            normal ? current.push(head.val) : current.unshift(head.val);\n            if (head.left !== null) { queue.push(head.left); }\n            if (head.right !== null) { queue.push(head.right); }\n        }\n        result.push(current);\n        normal = !normal;\n    }\n    return result;\n}\nconsole.log(zigzagLevelOrder(null));"