module.exports = "// Given a binary tree, return the preorder traversal of its nodes' values.\n\n// Example:\n\n// Input: [1,null,2,3]\n//    1\n//     \\\n//      2\n//     /\n//    3\n\n// Output: [1,2,3]\n\nfunction TreeNode(val){\n    this.val = val;\n    this.left = null;\n    this.right = null;\n}\n\nconst preorderTraversal = root => {\n    let result = [];\n    const traversal = node => {\n        if (node === null) return result;\n        result.push(node.val);\n        traversal(node.left);\n        traversal(node.right);\n    }\n    traversal(root);\n    return result;\n}\n\nconsole.log(preorderTraversal(null));\n\n\n// preorder without recusion\nconst preOrderTraversal = root => {\n    let stack = [];\n    let inorder = [];\n\n    stack.push(root);\n    while (stack.length > 0) {\n        let node = stack.pop();\n        inorder.push(node.val);\n        if (node.right !== null) { stack.push(node.right); }\n        if (node.left !== null) { stack.push(node.left); }\n    }\n    return inorder;\n}\n\nconst tn1 = new TreeNode(1);\nconst tn2 = new TreeNode(2);\nconst tn3 = new TreeNode(3);\nconst tn4 = new TreeNode(4);\nconst tn5 = new TreeNode(5);\n\ntn1.left = tn2;\ntn1.right = tn3;\ntn2.left = tn4;\ntn2.right = tn5;\n\n//       1\n//      / \\\n//     2   3\n//    / \\\n//   4   5\n\nconsole.log(preOrderTraversal(tn1));"