module.exports = "// Given a binary tree, return the bottom-up level order traversal of its nodes' values. (ie, from left to right, level by level from leaf to root).\n\n// For example:\n// Given binary tree [3,9,20,null,null,15,7],\n//     3\n//    / \\\n//   9  20\n//     /  \\\n//    15   7\n// return its bottom-up level order traversal as:\n// [\n//   [15,7],\n//   [9,20],\n//   [3]\n// ]\n\nconst levelOrderBottom = root => {\n    if (root === null) return [];\n    let result = [];\n    let queue = [root];\n    while(queue.length > 0) {\n        let size = queue.length;\n        let current = [];\n        for (let i = 0; i < size; i++) {\n            let head = queue.shift();\n            current.push(head.val);\n            if (head.left !== null) { queue.push(head.left); }\n            if (head.right !== null) { queue.push(head.right); }\n        }\n        result.unshift(current);\n    }\n    return result;\n}\nfunction TreeNode(val){\n    this.val = val;\n    this.right = null;\n    this.left = null;\n}\nconst n1 = new TreeNode(3);\nconst n2 = new TreeNode(9);\nconst n3 = new TreeNode(20);\nconst n4 = new TreeNode(15);\nconst n5 = new TreeNode(7);\n\nn1.left = n2;\nn1.right = n3;\nn3.left = n4;\nn3.right = n5;\nconsole.log(levelOrderBottom(n1));"