module.exports = "// Given a string containing just the characters '(', ')', '{', '}', '[' and ']', determine if the input string is valid.\n\n// An input string is valid if:\n\n// Open brackets must be closed by the same type of brackets.\n// Open brackets must be closed in the correct order.\n// Note that an empty string is also considered valid.\n\n\n// Input: \"()[]{}\"\n// Output: true\n// Example 3:\n\n// Input: \"(]\"\n// Output: false\n// Example 4:\n\n// Input: \"([)]\"\n// Output: false\n// Example 5:\n\nconst isValidParentheses = (lastChar, current) => {\n    return (lastChar === \"{\" && current === \"}\") || (lastChar === \"(\" && current === \")\") || (lastChar === \"[\" && current === \"]\")\n}\nvar isValid = function(s) {\n    let stack = [];\n    for (let i = 0; i < s.length; i++) {\n        if (\"({[\".includes(s[i])) {\n            stack.push(s[i]);\n        } else {\n            if (!isValidParentheses(stack[stack.length - 1], s[i])) { console.log('nn'); return false }\n            stack.pop();\n        }\n    }\n    return stack.length === 0;\n};\n\nconsole.log(isValid(\"{{}}\"));\nconsole.log(isValid(\"{}[]()\"));"