module.exports = "// // https://www.youtube.com/watch?v=vWPCm69MSfs\n\n// Given two words (beginWord and endWord), and a dictionary's word list, find the length of shortest transformation sequence from beginWord to endWord, such that:\n\n// Only one letter can be changed at a time.\n// Each transformed word must exist in the word list. Note that beginWord is not a transformed word.\n\n// Input:\n// beginWord = \"hit\",\n// endWord = \"cog\",\n// wordList = [\"hot\",\"dot\",\"dog\",\"lot\",\"log\",\"cog\"]\n\n// Output: 5\n\n// Explanation: As one shortest transformation is \"hit\" -> \"hot\" -> \"dot\" -> \"dog\" -> \"cog\", return its length 5.\n// -----------\n\n// Example 2:\n// Input:\n// beginWord = \"hit\"\n// endWord = \"cog\"\n// wordList = [\"hot\",\"dot\",\"dog\",\"lot\",\"log\"]\n\n// Output: 0\n\n// Explanation: The endWord \"cog\" is not in wordList, therefore no possible transformation.\n\n// function ladderLength(beginWord, endWord, wordList) {\n//     let len = 1;\n//     let result = [];\n//     let queue = [beginWord];\n//     const dict = new Set(wordList);\n//     const seen = new Set(queue);\n//     while (queue.length) {\n//       const next = [];\n//       for (let v of queue) {\n//         if (v === endWord) {\n//           console.log(result);\n//           return len;\n//         }\n        \n//         const arr = v.split('');\n//         for (let i = 0; i < arr.length; i++) {\n//           for (let d = 0; d < 26; d++) {\n//             arr[i] = String.fromCharCode(97+d);\n//             const nv = arr.join('');\n//             if (!seen.has(nv) && dict.has(nv)) {\n//               next.push(nv);\n//               seen.add(nv);\n//             }\n//             arr[i] = v[i];\n//           }\n//         }\n//         result.push(...next);\n//       }\n//       queue = next;\n//       len++;\n//     }\n    \n//     return 0;\n//   }\n\nconst swapLetterInWord = (string, index, replace) =>{\n  return string.substring(0, index) + replace + string.substring(index + 1);\n}\nconst ladderLength = (beginWord, endWorld, wordList) => {\n    wordList.splice(wordList.indexOf(beginWord), 1);\n    let level = 1;\n    let current = 1;\n    let next = 0;\n    let queue = [beginWord];\n    while(queue.length > 0) {\n      let word = queue.shift();\n      current--;\n      for (let i = 0; i < word.length; i++) {\n        for (let j = 'a'.charCodeAt(0); j < 'z'.charCodeAt(0); j++) {\n          let temp = swapLetterInWord(word, i, String.fromCharCode(j));\n          if (wordList.indexOf(temp) > -1) {\n            if (temp === endWorld) return level + 1;\n            next++;\n            queue.push(temp);\n            wordList.splice(wordList.indexOf(temp), 1);\n          }\n        }\n      }\n      if (current === 0) {\n        current = next;\n        next = 0;\n        level++;\n      }\n    }\n    return 0;\n}\nconsole.log(ladderLength(\"hit\", \"cog\", [\"hit\",\"hot\",\"dot\",\"dog\",\"lot\",\"log\",\"cog\"]));"