module.exports = "// Given an array where elements are sorted in ascending order, convert it to a height balanced BST.\n\n// For this problem, a height-balanced binary tree is defined as a binary tree in which the depth of the two subtrees of every node never differ by more than 1.\n\n// Example:\n\n// Given the sorted array: [-10,-3,0,5,9],\n\n// One possible answer is: [0,-3,9,-10,null,5], which represents the following height balanced BST:\n\n//       0\n//      / \\\n//    -3   9\n//    /   /\n//  -10  5\nconst TreeNode = (val) => {\n    this.val = val;\n    this.left = null;\n    this.right = null;\n}\nconst buildBST = (nums, start, end) => {\n    if (start > end) return null;\n    let mid = Math.floor((start + end) / 2);\n    let bst = new TreeNode(nums[mid]);\n    bst.left = buildBST(nums, start, mid - 1);\n    bst.right = buildBST(nums, mid + 1, end);\n    return bst;\n}\n\nconst sortedArrayToBST = nums => {\n    if (nums.length === 0) return null;\n    return buildBST(nums, 0, nums.length - 1);\n}\n\nconsole.log(sortedArrayToBST([123]));"