module.exports = "// Merge two sorted linked lists and return it as a new list. The new list should be made by splicing together the nodes of the first two lists.\n\n// Example:\n\n// Input: 1->2->4, 1->3->4\n// Output: 1->1->2->3->4->4\n\nconst ListNode = val => {\n    this.val = val;\n    this.next = null;\n}\nconst mergeTwoLists = (list1, list2) => {\n    let mergedList = new ListNode(0);\n    let prev = mergedList;\n    while(list1 !== null && list2 !== null) {\n        if (list1.val <= list2.val) {\n            prev.next = list1;\n            list1 = list1.next;\n        } else {\n            prev.next = list2;\n            list2 = list2.next;\n        }\n        prev = prev.next;\n    }\n    if (list1 === null) { prev.next = list2; }\n    if (list2 === null) { prev.next = list1;}\n    return mergedList.next;\n}\nconsole.log(mergeTwoLists(null,null));"