module.exports = "// You're given strings J representing the types of stones that are jewels, and S representing the stones you have.  Each character in S is a type of stone you have.  You want to know how many of the stones you have are also jewels.\n\n// The letters in J are guaranteed distinct, and all characters in J and S are letters. Letters are case sensitive, so \"a\" is considered a different type of stone from \"A\".\n\n// Example 1:\n\n// Input: J = \"aA\", S = \"aAAbbbb\"\n// Output: 3\n// Example 2:\n\n// Input: J = \"z\", S = \"ZZ\"\n// Output: 0\n\nconst numJewelsInStones = (jStone, sStone ) => {\n    let result = 0;\n    let map = {};\n    for (let j = 0; j < jStone.length; j++) {\n        map[jStone[j]] = true;\n    }\n\n    for (let s = 0; s < sStone.length; s++) {\n        if (map[sStone[s]]) { result++; }\n    }\n    return result;\n}\nconsole.log(numJewelsInStones(null,null));\n\nconst numJewelsInStones2 = (jStone, sStone ) => {\n    let result = 0;\n    for (let i = 0; i < sStone.length; i++) {\n        if (jStone.indexOf(sStone[i]) >= 0) {\n            result++;\n        }\n    }\n    return result;\n}\n\nconsole.log(numJewelsInStones2(['aB'],['aaBBBBaa']));"