module.exports = "// Given a linked list, determine if it has a cycle in it.\n\n// Follow up:\n// Can you solve it without using extra space?\n\nconst hasCycle = head => {\n    if (head === null) return false;\n    let slow = head;\n    let fast = head;\n    while(fast.next !== null && fast.next.next !== null) {\n        slow = slow.next;\n        fast = fast.next.next;\n        if (slow === fast) return true;\n    }\n    return false;\n}\n\nconsole.log(hasCycle(null));"