module.exports = "// Given a binary tree, return the postorder traversal of its nodes' values.\n\n// Example:\n\n// Input: [1,null,2,3]\n//    1\n//     \\\n//      2\n//     /\n//    3\n\n// Output: [3,2,1]\n\nvar postorderTraversal = function(root) {\n    let result = [];\n    const traversal = node => {\n        if (node === null) return result;\n        traversal(node.left);\n        traversal(node.right);\n        result.push(node.val);\n    }\n    traversal(root);\n    return result;\n}\nconsole.log(postorderTraversal(null));"