module.exports = "// Given a binary tree, return the inorder traversal of its nodes' values.\n\n// Example:\n\n// Input: [1,null,2,3]\n//    1\n//     \\\n//      2\n//     /\n//    3\n\n// Output: [1,3,2]\n\nfunction TreeNode(val){\n    this.val = val;\n    this.left = null;\n    this.right = null;\n}\nvar inorderTraversal = function(root) {\n    let result = [];\n    const traversal = head => {\n        if (head === null) return head;\n        traversal(head.left);\n        result.push(head.val);\n        traversal(head.right);\n    }\n    traversal(root);\n    return result;\n}\n\nconst tn1 = new TreeNode(1);\nconst tn2 = new TreeNode(2);\nconst tn3 = new TreeNode(3);\nconst tn4 = new TreeNode(4);\nconst tn5 = new TreeNode(5);\n\ntn1.left = tn2;\ntn1.right = tn3;\ntn2.left = tn4;\ntn2.right = tn5;\n\n//       1\n//      / \\\n//     2   3\n//    / \\\n//   4   5\n\nconsole.log(inorderTraversal(tn1));\n\n// -- InOrder Traversal without recursion\n//\n// 1) Create an empty stack S.\n// 2) Initialize current node as root\n// 3) Push the current node to S and set current = current->left until current is NULL\n// 4) If current is NULL and stack is not empty then \n//      a) Pop the top item from stack.\n//      b) Print the popped item, set current = popped_item->right \n//      c) Go to step 3.\n// 5) If current is NULL and stack is empty then we are done.\n\nconst inorderTraversal2 = (root) => \n{\n    let stack = [];\n    let inorder =[];\n    while (true)\n    {\n        while (root != null)\n        {\n            stack.push(root);\n            root = root.left;\n        }\n        if (stack.length === 0) break;\n        root = stack.pop();\n        inorder.push(root.val);\n        root = root.right;\n    }\n    return inorder;\n}\n\nconsole.log(inorderTraversal2(tn1));"