module.exports = "// The set [1,2,3,...,n] contains a total of n! unique permutations.\n\n// By listing and labeling all of the permutations in order, we get the following sequence for n = 3:\n\n// \"123\"\n// \"132\"\n// \"213\"\n// \"231\"\n// \"312\"\n// \"321\"\n// Given n and k, return the kth permutation sequence.\n\n// Note:\n\n// Given n will be between 1 and 9 inclusive.\n// Given k will be between 1 and n! inclusive.\n// Example 1:\n\n// Input: n = 3, k = 3\n// Output: \"213\"\n// Example 2:\n\n// Input: n = 4, k = 9\n// Output: \"2314\"\n\n/**\n * @param {number} n\n * @param {number} k\n * @return {string}\n */\nconst dfs = (nums, list, result) => {\n    // sanity check\n    if (list.length === nums.length) { result.push(list.join('')); }\n\n    // core logic\n    for (let i = 0; i < nums.length; i++) {\n        if (list.includes(nums[i])) { continue; }\n        list.push(nums[i]);\n        dfs(nums, list, result );\n        list.pop();\n    }\n}\n\nvar getPermutation = function(n, k) {\n    // sanity check\n    if (n === 0 || k === 0) return [];\n    \n    let result = [];\n    let uniquePermutation = [];\n    for (let i = 1; i <= n; i++) {\n        uniquePermutation.push(i);\n    }\n    \n    dfs(uniquePermutation, [], result);\n    \n    return result[k - 1];\n};\n\nconsole.log(getPermutation(3,3));\nconsole.log(getPermutation(4,9));"