module.exports = "// Sort a linked list in O(n log n) time using constant space complexity.\n\n// Example 1:\n\n// Input: 4->2->1->3\n// Output: 1->2->3->4\n// Example 2:\n\n// Input: -1->5->3->4->0\n// Output: -1->0->3->4->5\n \n// const ListNode = (val) => {\n//     this.val = val;\n//     this.next = null;\n// }\nfunction ListNode(val) {\n    this.val = val;\n    this.next = null; \n}\nconst mergeTwoLists = (list1, list2) => {\n    let mergeTwoList = new ListNode(0);\n    let current = mergeTwoList;\n    while(list1 !== null && list2 !== null) {\n        if (list1.val <= list2.val) {\n            current.next = list1;\n            list1 = list1.next;\n        } else {\n            current.next = list2;\n            list2 = list2.next;\n        }\n        current = current.next;\n    }\n    if (list1 === null) { current.next = list2; }\n    if (list2 === null) { current.next = list1; }\n    return mergeTwoList.next;\n}\nconst sortList = head => {\n    if (head === null || head.next === null) return head;\n    let pre = head, slow = head, fast = head;\n    while(fast !== null && fast.next !== null) {\n        pre = slow;\n        slow = slow.next;\n        fast = fast.next.next;\n    }\n    pre.next = null;\n    return mergeTwoLists(sortList(head), sortList(slow))\n}\n\nconst n1 = new ListNode(1);\nconst n2 = new ListNode(12);\nconst n3 = new ListNode(3);\nconst n4 = new ListNode(24);\nconst n5 = new ListNode(5);\nconst n6 = new ListNode(36);\nconst n7 = new ListNode(7);\n\nn1.next = n2;\nn2.next = n3;\nn3.next = n4;\nn4.next = n5;\nn5.next = n6;\nn6.next = n7;\n\nconsole.log(n1);\n\nconsole.log(sortList(n1));"