module.exports = "// You are given coins of different denominations and a total amount of money amount. Write a function to compute the fewest number of coins that you need to make up that amount. If that amount of money cannot be made up by any combination of the coins, return -1.\n\n// Example 1:\n\n// Input: coins = [1, 2, 5], amount = 11\n// Output: 3 \n// Explanation: 11 = 5 + 5 + 1\n\n/**\n * @param {number[]} coins\n * @param {number} amount\n * @return {number}\n */\nvar coinChange = function(coins, amount) {\n    var dp=new Array(amount+1);\n    dp.fill(Number.MAX_VALUE-1);\n    dp[0]=0;\n    for(var i=1;i<=amount;i++)\n    {\n        for(var j=0;j<coins.length;j++) {\n            if(coins[j]<=i){\n                dp[i]=Math.min(dp[i-coins[j]]+1, dp[i]);\n            }\n        }\n    }\n   return dp[amount] === Number.MAX_VALUE-1 ? -1:dp[amount];  \n};\n\nconsole.log(coinChange(12));"