module.exports = "// Write a function that takes a string as input and returns the string reversed.\n\n// Example 1:\n\n// Input: \"hello\"\n// Output: \"olleh\"\n// Example 2:\n\n// Input: \"A man, a plan, a canal: Panama\"\n// Output: \"amanaP :lanac a ,nalp a ,nam A\"\n\nvar reverseString = function(s) {\n    var newString = '';\n    for (let i = s.length - 1; i >= 0 ; i --) {\n        newString += s[i];\n    }\n    return newString;\n};\n\nconsole.log(reverseString('123'));"