module.exports = "// Remove all elements from a linked list of integers that have value val.\n\n// Example:\n\n// Input:  1->2->6->3->4->5->6, val = 6\n// Output: 1->2->3->4->5\n\n/**\n * @param {ListNode} head\n * @param {number} val\n * @return {ListNode}\n */\nconst ListNode = (val) => {\n    this.val = val;\n    this.next = null;\n}\nvar removeElements = (head, val) => {\n    let dummyNode = new ListNode(0);\n    dummyNode.next = head;\n    head = dummyNode;\n    while(head.next !== null) {\n        if (head.next.val === val) {\n            head.next = head.next.next;\n        } else {\n            head = head.next;\n        }\n    }\n    return dummyNode.next\n}\nconsole.log(removeElements(null,null));"