module.exports = "// Given a set of distinct integers, nums, return all possible subsets (the power set).\n\n// Note: The solution set must not contain duplicate subsets.\n\n// Example:\n\n// Input: nums = [1,2,3]\n// Output:\n// [\n//   [3],\n//   [1],\n//   [2],\n//   [1,2,3],\n//   [1,3],\n//   [2,3],\n//   [1,2],\n//   []\n// ]\n\n// time = O(2^n)\n// space = O(n)\n\nconst dfs = (nums, startIndex, list, result) => {\n    // sanity check and return the data to stop the recursion\n    if(startIndex === nums.length) { result.push(list.slice()); return; }\n\n    // core logic\n    \n    dfs(nums, startIndex + 1, list, result);\n    list.push(nums[startIndex]);\n    dfs(nums, startIndex + 1, list, result);\n    list.pop();\n}\n\nvar subsets = function(nums) {\n    // sanity check\n    if (nums === null || nums.length === 0) return [];\n\n    let result = [];\n\n    // depth first search\n    dfs(nums, 0, [], result);\n\n    // return subset;\n    return result;\n};\n\nconsole.log(subsets([1,2,3]));\n\n// -------------------------------------------\n// [\n//  [],\n//  [3],\n//  [2],\n//  [2,3],\n//  [1],\n//  [1,3]\n//  [1,2]\n//  [1,2,3],\n// ]\nconst dfs2 = (nums, startIndex, subset, result) =>{\n    result.push(subset.slice());\n    for (let i = startIndex; i < nums.length; i++) {\n        subset.push(nums[i]);\n        dfs2(nums, startIndex, subset, result);\n        subset.pop();\n    }\n}\nconst subset2 = nums => {\n    if (nums === null || nums.length === 0)  return [];\n    \n    let result = [];\n    dfs2(nums, 0, [], result);\n    return result;\n}\n\nconsole.log(subset2([1,2,3]));"