module.exports = "// Given a string, determine if it is a palindrome, considering only alphanumeric characters and ignoring cases.\n\n// Note: For the purpose of this problem, we define empty string as valid palindrome.\n\n// Example 1:\n\n// Input: \"A man, a plan, a canal: Panama\"\n// Output: true\n// Example 2:\n\n// Input: \"race a car\"\n// Output: false\n\nconst isPalindrome = str => {\n    const tempStr = str.replace(/[^\\w]/g, '').toLowerCase();\n    let left = 0;\n    let right = tempStr.length - 1;\n    while (left < right) {\n        if (tempStr[left] !== tempStr[right]) return false;\n        left++;\n        right--;\n    }\n    return true;\n}\nconsole.log(isPalindrome(''));"