module.exports = "// Given a binary tree, imagine yourself standing on the right side of it, return the values of the nodes you can see ordered from top to bottom.\n\n// Example:\n\n// Input: [1,2,3,null,5,null,4]\n// Output: [1, 3, 4]\n// Explanation:\n\n//    1            <---\n//  /   \\\n// 2     3         <---\n//  \\     \\\n//   5     4       <---\n\n\nconst rightSideView = root => {\n    if (root === null) { return []; }\n    let result = [];\n    let queue = [root];\n    while (queue.length > 0) {\n        let size = queue.length;\n        let current = [];\n        for (let i = 0; i < size; i++) {\n            let head = queue.shift();\n            current.push(head.val);\n            if (head.left !== null) { queue.push(head.left); }\n            if (head.right !== null) { queue.push(head.right); }\n        }\n        result.push(current[current.length - 1]);\n    }\n    return result;\n}\n\nconsole.log(rightSideView(null));\n"