module.exports = "// Given an array of size n, find the majority element. The majority element is the element that appears more than ⌊ n/2 ⌋ times.\n\n// You may assume that the array is non-empty and the majority element always exist in the array.\n\n// Example 1:\n\n// Input: [3,2,3]\n// Output: 3\n// Example 2:\n\n// Input: [2,2,1,1,1,2,2]\n// Output: 2\n\nvar majorityElement = function(nums) {\n    let map = {};\n    let result = nums[0];\n    let time = 0;\n    for (let i = 0; i < nums.length; i++) {\n        map[nums[i]] = map[nums[i]] === undefined ? 0 : map[nums[i]] + 1;\n        if (map[nums[i]] > time) {\n            result = nums[i];\n            time = map[nums[i]];\n        }\n    }\n    return result;\n};\n\nconsole.log(majorityElement([1,1,3,3,3,3,3]));\n\n\n\n"