module.exports = "// Given the root node of a binary search tree (BST) and a value. You need to find the node in the BST that the node's value equals the given value. Return the subtree rooted with that node. If such node doesn't exist, you should return NULL.\n\n// For example, \n\n// Given the tree:\n//         4\n//        / \\\n//       2   7\n//      / \\\n//     1   3\n\n// And the value to search: 2\n// You should return this subtree:\n\n//       2     \n//      / \\   \n//     1   3\n// In the example above, if we want to search the value 5, since there is no node with value 5, we should return NULL.\n\n// Note that an empty tree is represented by NULL, therefore you would see the expected output (serialized tree format) as [], not null.\n\nconst searchBST = (root, val) => {\n    if (root === null) return root;\n    if (root.val === val) { return root; }\n    else if (val < root.val && root.left !== null) { return searchBST(root.left, val); }\n    else if (val > root.val && root.right !== null) { return searchBST(root.right, val); }\n    else { return []; }\n}"