module.exports = "// Given a non-empty array of digits representing a non-negative integer, plus one to the integer.\n\n// The digits are stored such that the most significant digit is at the head of the list, and each element in the array contain a single digit.\n\n// You may assume the integer does not contain any leading zero, except the number 0 itself.\n\n// Example 1:\n\n// Input: [1,2,3]\n// Output: [1,2,4]\n// Explanation: The array represents the integer 123.\n// Example 2:\n\n// Input: [4,3,2,1]\n// Output: [4,3,2,2]\n// Explanation: The array represents the integer 4321.\n\n/**\n * @param {number[]} digits\n * @return {number[]}\n */\n\nvar plusOne = function(digits) {\n    let result = [];\n    let current = 0;\n    let carryOver = 0;\n    for (let i = digits.length - 1; i >= 0; i--) {\n        current = digits[i] + carryOver;\n        if (i ===  digits.length - 1) {\n            current = current + 1;\n        }\n        if (Math.floor(current / 10) > 0) {\n            carryOver = Math.floor(current / 10);\n            result.unshift(current % 10);\n        } else {\n            result.unshift(current);\n            carryOver = 0;\n        }\n    }\n    if (carryOver > 0) {\n        result.unshift(carryOver);\n    }\n    return result;\n}; \nconsole.log(plusOne([9,9]));"