module.exports = "// International Morse Code defines a standard encoding where each letter is mapped to a series of dots and dashes, as follows: \"a\" maps to \".-\", \"b\" maps to \"-...\", \"c\" maps to \"-.-.\", and so on.\n\n// For convenience, the full table for the 26 letters of the English alphabet is given below:\n\n// [\".-\",\"-...\",\"-.-.\",\"-..\",\".\",\"..-.\",\"--.\",\"....\",\"..\",\".---\",\"-.-\",\".-..\",\"--\",\"-.\",\"---\",\".--.\",\"--.-\",\".-.\",\"...\",\"-\",\"..-\",\"...-\",\".--\",\"-..-\",\"-.--\",\"--..\"]\n// Now, given a list of words, each word can be written as a concatenation of the Morse code of each letter. For example, \"cab\" can be written as \"-.-.-....-\", (which is the concatenation \"-.-.\" + \"-...\" + \".-\"). We'll call such a concatenation, the transformation of a word.\n\n// Return the number of different transformations among all words we have.\n\n// Example:\n// Input: words = [\"gin\", \"zen\", \"gig\", \"msg\"]\n// Output: 2\n// Explanation: \n// The transformation of each word is:\n// \"gin\" -> \"--...-.\"\n// \"zen\" -> \"--...-.\"\n// \"gig\" -> \"--...--.\"\n// \"msg\" -> \"--...--.\"\n\n// There are 2 different transformations, \"--...-.\" and \"--...--.\".\n\nvar uniqueMorseRepresentations = function(words) {\n    let morseCodeLookup = [\".-\",\"-...\",\"-.-.\",\"-..\",\".\",\"..-.\",\"--.\",\"....\",\"..\",\".---\",\"-.-\",\".-..\",\"--\",\"-.\",\"---\",\".--.\",\"--.-\",\".-.\",\"...\",\"-\",\"..-\",\"...-\",\".--\",\"-..-\",\"-.--\",\"--..\"];\n    let translatedWords = [];\n    words.forEach(word => {\n        let wordSplit = word.split('');\n        let compiledWord = '';\n        wordSplit.forEach((character, idx) => {\n            compiledWord += morseCodeLookup[word.charCodeAt(idx) - 97];\n        })\n        translatedWords.push(compiledWord);\n    });\n    \n    let filteredWords = [];\n    translatedWords.forEach(word => {\n        if(!filteredWords.includes(word)) filteredWords.push(word);\n    })\n    return filteredWords.length;\n};\n\nconsole.log(uniqueMorseRepresentations(''));"