module.exports = "// Given a string s, partition s such that every substring of the partition is a palindrome.\n\n// Return all possible palindrome partitioning of s.\n\n// Example:\n\n// Input: \"aab\"\n// Output:\n// [\n//   [\"aa\",\"b\"],\n//   [\"a\",\"a\",\"b\"]\n// ]\n\n/**\n * @param {string} s\n * @return {string[][]}\n */\n\nconst isPalidrome = str => {\n    let start = 0;\n    let end = str.length - 1;\n    while ( start <= end) {\n        if (str[start] !== str[end]) { return false}\n        start++;\n        end--;\n    }\n    return true;\n}\nconst helper = (str, startIndex, partition, result) => {\n    // sanity check\n    if (startIndex === str.length) {\n        result.push(partition.slice());\n        return;\n    }\n    // core logic\n    for (let i = startIndex; i < str.length; i++) {\n        let subStr = str.slice(startIndex, i + 1);\n        if (!isPalidrome(subStr)) {\n            continue;\n        }\n\n        partition.push(subStr);\n        helper(str, i + 1, partition, result);\n        partition.pop();\n    }\n}\n\nconst partition = (str) => {\n    // sanity check\n    if (str === null || str.length === 0) return [];\n  \n    // create result array\n    let result = [];\n\n    // base logic - recursive\n    helper(str, 0, [], result);\n\n    // return\n    return result;\n};\n\nconsole.log(partition('aab'));"